module.exports = "import React, {Component} from 'react';\nimport {Button, FormControlLabel, MenuItem, Radio, Typography} from '@material-ui/core';\nimport {TextFieldFormsy, CheckboxFormsy, RadioGroupFormsy, SelectFormsy} from '@fuse';\nimport Formsy from 'formsy-react';\n\nclass SimpleFormExample extends Component {\n    state = {\n        canSubmit: false\n    };\n\n    disableButton = () => {\n        this.setState({canSubmit: false});\n    };\n\n    enableButton = () => {\n        this.setState({canSubmit: true});\n    };\n\n    onSubmit = (model) => {\n        console.info('submit', model);\n    };\n\n    render()\n    {\n        const {canSubmit} = this.state;\n\n        return (\n            <div className=\"max-w-sm\">\n                <Typography className=\"h2 mb-24\">Example Formsy Form</Typography>\n                <Formsy\n                    onValidSubmit={this.onSubmit}\n                    onValid={this.enableButton}\n                    onInvalid={this.disableButton}\n                    ref={(form) => this.form = form}\n                    className=\"flex flex-col justify-center\"\n                >\n\n                    <TextFieldFormsy\n                        className=\"mb-16\"\n                        type=\"text\"\n                        name=\"name\"\n                        label=\"Name\"\n                        validations={{\n                            minLength: 4\n                        }}\n                        validationErrors={{\n                            minLength: 'Min character length is 4'\n                        }}\n                        required\n                    />\n\n                    <TextFieldFormsy\n                        className=\"my-16\"\n                        type=\"text\"\n                        name=\"email\"\n                        label=\"Email\"\n                        validations=\"isEmail\"\n                        validationError=\"This is not a valid email\"\n                        required\n                    />\n\n                    <TextFieldFormsy\n                        className=\"my-16\"\n                        type=\"text\"\n                        name=\"name\"\n                        label=\"Name (Outlined)\"\n                        validations={{\n                            minLength: 4\n                        }}\n                        validationErrors={{\n                            minLength: 'Min character length is 4'\n                        }}\n                        required\n                        variant=\"outlined\"\n                    />\n\n                    <TextFieldFormsy\n                        className=\"my-16\"\n                        type=\"text\"\n                        name=\"email\"\n                        label=\"Email (Outlined)\"\n                        validations=\"isEmail\"\n                        validationError=\"This is not a valid email\"\n                        required\n                        variant=\"outlined\"\n                    />\n\n                    <TextFieldFormsy\n                        className=\"my-16\"\n                        type=\"text\"\n                        name=\"name\"\n                        label=\"Name (Filled)\"\n                        validations={{\n                            minLength: 4\n                        }}\n                        validationErrors={{\n                            minLength: 'Min character length is 4'\n                        }}\n                        required\n                        variant=\"filled\"\n                    />\n\n                    <TextFieldFormsy\n                        className=\"my-16\"\n                        type=\"text\"\n                        name=\"email\"\n                        label=\"Email (Filled)\"\n                        validations=\"isEmail\"\n                        validationError=\"This is not a valid email\"\n                        required\n                        variant=\"filled\"\n                    />\n\n                    <RadioGroupFormsy\n                        className=\"my-16\"\n                        name=\"gender\"\n                        label=\"Gender\"\n                        validations=\"equals:female\"\n                        validationError=\"Only ladies are accepted\"\n                        required\n                    >\n                        <FormControlLabel value=\"male\" control={<Radio color=\"primary\"/>} label=\"Male\"/>\n                        <FormControlLabel value=\"female\" control={<Radio color=\"primary\"/>} label=\"Female\"/>\n                        <FormControlLabel value=\"other\" control={<Radio color=\"primary\"/>} label=\"Other\"/>\n                        <FormControlLabel\n                            value=\"disabled\"\n                            disabled\n                            control={<Radio/>}\n                            label=\"(Disabled option)\"\n                        />\n                    </RadioGroupFormsy>\n\n                    <SelectFormsy\n                        className=\"my-16\"\n                        name=\"related\"\n                        label=\"Related with\"\n                        value=\"none\"\n                        validations=\"equals:none\"\n                        validationError=\"Must be None\"\n                    >\n                        <MenuItem value=\"none\">\n                            <em>None</em>\n                        </MenuItem>\n                        <MenuItem value=\"hai\">Hai</MenuItem>\n                        <MenuItem value=\"olivier\">Olivier</MenuItem>\n                        <MenuItem value=\"kevin\">Kevin</MenuItem>\n                    </SelectFormsy>\n\n                    <SelectFormsy\n                        className=\"my-16\"\n                        name=\"related\"\n                        label=\"Related with (Outlined)\"\n                        value=\"none\"\n                        validations=\"equals:none\"\n                        validationError=\"Must be None\"\n                        variant=\"outlined\"\n                    >\n                        <MenuItem value=\"none\">\n                            <em>None</em>\n                        </MenuItem>\n                        <MenuItem value=\"hai\">Hai</MenuItem>\n                        <MenuItem value=\"olivier\">Olivier</MenuItem>\n                        <MenuItem value=\"kevin\">Kevin</MenuItem>\n                    </SelectFormsy>\n\n                    <SelectFormsy\n                        className=\"my-16\"\n                        name=\"related\"\n                        label=\"Related with (Filled)\"\n                        value=\"none\"\n                        validations=\"equals:none\"\n                        validationError=\"Must be None\"\n                        variant=\"filled\"\n                    >\n                        <MenuItem value=\"none\">\n                            <em>None</em>\n                        </MenuItem>\n                        <MenuItem value=\"hai\">Hai</MenuItem>\n                        <MenuItem value=\"olivier\">Olivier</MenuItem>\n                        <MenuItem value=\"kevin\">Kevin</MenuItem>\n                    </SelectFormsy>\n\n                    <CheckboxFormsy\n                        className=\"my-16\"\n                        name=\"accept\"\n                        label=\"Accept\"\n                        validations=\"equals:true\"\n                        validationError=\"You need to accept\"\n                    />\n\n                    <Button\n                        type=\"submit\"\n                        variant=\"contained\"\n                        color=\"primary\"\n                        className=\"mx-auto my-16\"\n                        aria-label=\"LOG IN\"\n                        disabled={!canSubmit}\n                    >\n                        Can submit\n                    </Button>\n                </Formsy>\n            </div>\n        );\n    }\n}\n\nexport default SimpleFormExample;\n"